import { FAIL_KASSA_TABLE } from 'redux/typeKassa'
import { DELETE_KASSA_TABLE } from 'redux/typeKassa'
import { CREATE_KASSA_TABLE } from 'redux/typeKassa'
import { GET_KASSA_TABLE } from 'redux/typeKassa'
import axios from '../../../axios'
export const getKassaData = () => async (dispatch) => {
    try {
        
        const { data } = await axios.get('finance/table/')
        dispatch({
            type: GET_KASSA_TABLE,
            payload: data
        })
      
 
    } catch (error) {
        dispatch({
            type: FAIL_KASSA_TABLE,
            payload: error.response && error.response.data.detail
                ? error.response.data.detail
                : error.message,
        })
    }
}

export const createKassaData = (table) => async (dispatch) => {
    try {
        
        // const { data } = await axios.post('finance/table/',{
        //       name:manba
        // })

        dispatch({
            type: CREATE_KASSA_TABLE,
            payload: table
        })
        // localStorage.setItem('table', JSON.stringify(manba))
 
    } catch (error) {
        dispatch({
            type: FAIL_KASSA_TABLE,
            payload: error.response && error.response.data.detail
                ? error.response.data.detail
                : error.message,
        })
    }
}

export const deleteKassaRow = (id) => async (dispatch, getState) => {
    try {
        const { data } = await axios.delete(
            `finance/table/${id}/`,
       
        )
        dispatch({
            type: DELETE_KASSA_TABLE,
            id:id,
            payload:data
        })

        const {
            userLogin: { userInfo },
        } = getState()

        const config = {
            headers: {
                'Content-type': 'application/json',
                Authorization: `Bearer ${userInfo.token}`
            }
        }

     

    

    } catch (error) {
       console.log(error)
    }
}

