import React, { useState } from 'react';
import { NavItem, NavLink } from 'reactstrap';
import { NavLink as NavLinkRRD } from 'react-router-dom';
import { useSelector } from 'react-redux';
import './Dropdown.css';
function Dropdown({ item = 0 }) {
	const [close, setClose] = useState(false);
	const { userInfo } = useSelector((state) => state.userLogin);
	// const activeRoute = (routeName) => {
	// 	return props.location.pathname.indexOf(routeName) > -1 ? 'active' : '';
	// };
	const handleClose = (e) => {
		setClose(!close);
	};
	return (
		<div>
			<button className="dropdown-btn drop-btn-res" onClick={handleClose}>
				<i className="far fa-credit-card text-primary " style={{ paddingRight: 20 }}></i>
				Хисобот
				<i className="fa fa-caret-down pl-1"></i>
			</button>
			<div className={`dropdown-container ${close && 'openDrop'}`}>
				<NavItem>
					{userInfo?.authenticatedUser?.role == 2 ? (
						<NavLink to={'/app/hisobot/table'} tag={NavLinkRRD} activeClassName="active">
							<i className="fas fa-table  text-primary ml-2"></i>
							Касса жадвал
						</NavLink>
					) : (
						''
					)}
					{userInfo?.authenticatedUser?.role == 3 ? (
						<NavLink to={'/app/hisobot/xaridtable'} tag={NavLinkRRD} activeClassName="active">
							<i className="fas fa-table  text-primary ml-2"></i>
							Харид жадвал
						</NavLink>
					) : (
						''
					)}
				</NavItem>
			</div>
		</div>
	);
}

export default Dropdown;
