import {
  CREATE_KASSA_TABLE,
  DELETE_KASSA_TABLE,
  GET_KASSA_TABLE
     
   } from '../../typeKassa'
   const initialState = {
     kassa:[{
       1:"bir"
     }]
   }
   
   export const kassaTableReducer = (state=initialState, action) => {
       switch (action.type) {
           case GET_KASSA_TABLE:
             return{
               ...state,
               kassa:action.payload
             }
 
           case CREATE_KASSA_TABLE:
             
               return { 
                   ...state,
                   kassa:[...state.kassa,action.payload]
                     }
         
           case DELETE_KASSA_TABLE:
             const id = action.id    
              return {
           ...state,
         kassa: state.kassa.filter((card) => card.id !== id)
             }
           default:
               return state
       }
   }
   
   
   

   