import React, { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Card, CardHeader, Table, Row, Col } from 'reactstrap';
import ReactHTMLTableToExcel from 'react-html-table-to-excel';
import { postXaridData } from 'redux/action/xarid/tableActions';
import RowXarid from './RowXarid';
const KassaTable = () => {
	const dispatch = useDispatch();
	const { table } = useSelector((state) => state.xaridTable);
	console.log('====================================');
	console.log(table);
	console.log('====================================');
	useEffect(() => {
		dispatch(postXaridData());
	}, []);

	return (
		<Row className="mt-5">
			<Col xl="12">
				<Card className="shadow" style={{ height: '100vh', padding: 10 }}>
					<CardHeader className="border-0">
						<Row className="align-items-center">
							<div className="col">
								<h3 className="mb-0">Жадвал</h3>
							</div>
							<div className="col text-right">
								<ReactHTMLTableToExcel
									id="test-table-xls-button"
									className="download-table-xls-button excel"
									table="table-to-xls"
									filename="Xarid table"
									sheet="tablexls"
									buttonText={'Excelga saqlash'}
								/>
							</div>
						</Row>
					</CardHeader>
					<Table id="table-to-xls" className="align-items-center table-flush" responsive>
						<thead className="thead-light">
							<tr>
								<th>ID</th>
								<th>Харид номери</th>
								<th>Сана</th>
								<th>Контрагент</th>
								<th>Маҳсулот номи</th>
								<th>Сўйилган моллар сони</th>
								<th>Миқдори</th>
								<th>Гўшт йўқотилиши</th>
								<th>Нарх</th>
								<th>Сотиб олиниш сумма</th>
								<th>НДС тўлов</th>
								<th>Ветеринария</th>
								<th>Гостандарт</th>
								<th>Гигиена</th>
								<th>Таможний</th>
								<th>Декларант</th>
								<th>Доставкага тўлов</th>
								<th>СВИФТ</th>
								<th>Молия вазирлиги</th>
								<th>Сертификат</th>
								<th>СПОТ</th>
								<th>СЭС</th>
								<th> Жами суммаси</th>
								<th>Тўлов тури</th>
								<th>Фура холати</th>
								<th>Тугаган сана</th>
								<th>Тўланди </th>
								<th>Қолдиқ</th>
								<th>Сотиб олинган ой</th>
								<th>Тугаган ой</th>
								<th>Изох</th>
							</tr>
						</thead>
						<tbody>
							{table.map((item) => (
								<RowXarid row={item} />
							))}
						</tbody>
					</Table>
				</Card>
			</Col>
		</Row>
	);
};

export default KassaTable;
